const {ccclass, property} = cc._decorator;

@ccclass
export default class LoginScreen extends cc.Component {

    @property(cc.Node)
    nodeLayoutLogin: cc.Node = null;
    @property(cc.Node)
    nodeLayoutSignIn: cc.Node = null;

    start () {
        this.nodeLayoutLogin.active = true;
        this.nodeLayoutSignIn.active = false;
        window.fbAsyncInit = function() {
            FB.init({
              appId      : '577254864321632',
              cookie     : true,                     // Enable cookies to allow the server to access the session.
              xfbml      : true,                     // Parse social plugins on this webpage.
              version    : 'v3.3.6'           // Use this Graph API version for this call.
            });
        }
    }

    onButtonSignInClick(): void {
        this.nodeLayoutLogin.active = false;
        this.nodeLayoutSignIn.active = true;
    }

    onButtonSignWithFBClick(): void {
        FB.getLoginStatus((response) => {
            this.statusChangeCallback(response);
        });
    }

    statusChangeCallback(response) {  // Called with the results from FB.getLoginStatus().
        console.log('statusChangeCallback');
        console.log(response);                   // The current login status of the person.
        if (response.status === 'connected') {   // Logged into your webpage and Facebook.
          this.testAPI();  
        } else {                                 // Not logged into your webpage or we are unable to tell.
        //   document.getElementById('status').innerHTML = 'Please log ' +
        //     'into this webpage.';
            FB.login(function(response) {
                if (response.authResponse) {
                console.log('Welcome!  Fetching your information.... ');
                FB.api('/me', function(response) {
                console.log('Good to see you, ' + response.name + '.');
                });
                } else {
                console.log('User cancelled login or did not fully authorize.');
                }
            });
        }
    }

    testAPI() {                      // Testing Graph API after login.  See statusChangeCallback() for when this call is made.
        console.log('Welcome!  Fetching your information.... ');
        FB.api('/me', function(response) {
          console.log('Successful login for: ' + response.name);
          document.getElementById('status').innerHTML =
            'Thanks for logging in, ' + response.name + '!';
        });
    }

}
